import forum_conf from '../assets/configs/forum/conf.json'
import forum_optimization_conf from '../assets/configs/forum/forum.optimization.json'
import Vue from "vue";

Vue.prototype.$forum_conf = forum_conf
Vue.prototype.$forum_optimization_conf = forum_optimization_conf

console.log('load forum_optimization_conf:',forum_optimization_conf);

Vue.prototype.save_topic_data = function (data) {
  var key = 'topic:' + data.id;
  console.log("save_topic_data:",key,data);
  localStorage.setItem(key,JSON.stringify(data));
}

Vue.prototype.get_topic_data_from_store = function (id) {
  var key = 'topic:' + id;
  return JSON.parse(localStorage.getItem(key))
}